<template>
  <div class="recommend">
    <ul class="recommend_list">
      <li v-for="item in recommendations" :key="item.id" class="recommend_item">
        <img :src="item.path" v-if="item.path"  @click="$router.push('/movie/' + item.id)" class="item_pic">
        <div class="default_pic_container" v-else>
          <Icon name="picture" class="default_pic"></Icon>
        </div>
        <div class="info_container">
          <span>{{item.title}}</span>
          <span>{{item.release_date}}</span>
          <Progress :percentage="item.vote_average * 10" :width="40" :strokeWidth="4" />
          <MarkBundle :movieDetail="normalize(item)" />
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Icon from '../../common/Icon'
import Progress from '../../common/Progress'
import MarkBundle from '../../common/MarkBundle'
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      recommendations: state => state.account.recommendations
    })
  },
  components: {
    Icon,
    Progress,
    MarkBundle
  },
  methods: {
    normalize (item) {
      // eslint-disable-next-line
      const { id, path, title, release_date, vote_average, vote_count } = item
      return {
        id,
        poster: path,
        title,
        release_date,
        vote_average,
        vote_count
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../../style/mixin';
.recommend_list {
  margin: 0 1rem;
  padding: 0;
  list-style: none;
}
.recommend_item {
  position: relative;
  height: 7.5769rem;
  margin-bottom: 1.5rem;
  display: flex;
  .item_pic, .default_pic_container {
    @include wh(5.1154rem, 7.5769rem);
    margin-right: 0.5rem;
  }
  .info_container {
    @include sc(0.6rem, #fff);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
}
</style>



